import React from 'react'
import PropTypes from 'prop-types'

import { Slider, Row, Col } from 'antd';
import debounce from 'lodash.debounce'

class RangeSlider extends React.Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);

    //value = [100,1100];
    this.state = { 
      value: props.value
    }; 
  }

  handleChange([vmin,vmax]) {

    this.setState( { value: [vmin,vmax] } );

    this.props.onFilterChange({
      minprice: vmin,
      maxprice: vmax
    });
  }

  render() {

    console.log("RangeSlider::render()");

    const [ lmin, lmax ] = this.props.limits;

//    const [ vmin, vmax ] = this.props.value;
//          value={[vmin, vmax]} 
    const [ vmin, vmax ] = this.state.value;

    let marks;
    if (lmin != lmax) {
      marks = {
        [lmin]: '' + lmin,
        [lmax]: '' + lmax
      }
    }else{
      marks = {
        [lmin]: '' + lmin
      }
    }

    return (
      <React.Fragment>
        <Row>
          <Col>
            {this.props.title}
          </Col>
          <Col>
            { `${vmin} - ${vmax}` }
          </Col>
        </Row>
        <Row>
          <Slider range included 
            min={lmin}
            max={lmax}
            marks={marks}
            value={this.state.value} 
            onCnange={debounce(this.handleChange,700)}
          />
        </Row>
      </React.Fragment>
    );
  }
};

RangeSlider.propTypes = {
  title: PropTypes.string.isRequired,
  limits: PropTypes.array.isRequired,
  value: PropTypes.array.isRequired,
  onFilterChange: PropTypes.func.isRequired
};

export default RangeSlider;



